import React from 'react';
import { Helmet } from 'react-helmet-async';
import { TeamMember } from '../types/Team';
import { teamData } from '../data/teamData';
import { useNavigate } from 'react-router-dom';

const Team: React.FC = () => {
  const navigate = useNavigate();
  const teamMembers: TeamMember[] = teamData;

  const numberToChineseUpperCase = (num: number): string => {
    const digits = ['', '', '', '', '', '', '', '', '', ''];
    const units = ['', '', '', '', '', '', '', '', ''];
    
    // 
    const convertNumber = (num: number): string => {
      if (!num || isNaN(num)) return '';
      if (num === 0) return '';
      
      try {
        const numStr = Math.floor(num).toString();
        let result = '';
        
        // 
        const len = numStr.length;
        const yi = Math.floor(len > 8 ? parseInt(numStr.slice(0, len - 8)) : 0);    // 
        const wan = Math.floor(len > 4 ? parseInt(numStr.slice(Math.max(0, len - 8), len - 4)) : 0);  // 
        const ge = Math.floor(parseInt(numStr.slice(Math.max(0, len - 4))));    // 
        
        // 
        if (yi > 0) {
          result += convertNumber(yi) + '';
        }
        
        // 
        if (wan > 0) {
          if (yi > 0 && wan < 1000) result += '';
          result += convertNumber(wan) + '';
        }
        
        // 
        if (ge > 0) {
          if ((yi > 0 || wan > 0) && ge < 1000) result += '';
          const geStr = ge.toString();
          for (let i = 0; i < geStr.length; i++) {
            const digit = parseInt(geStr[i]);
            const position = geStr.length - 1 - i;
            
            if (digit !== 0) {
              result += digits[digit] + (position > 0 ? units[position] : '');
            } else if (position > 0 && result[result.length - 1] !== '') {
              result += '';
            }
          }
        } else if (yi > 0 || wan > 0) {
          // 0
        } else {
          result = '';
        }
        
        // 
        result = result.replace(/+$/, '');
        result = result.replace(/+/g, '');
        
        return result;
      } catch (error) {
        console.error(':', error);
        return '';
      }
    };

    // 
    return convertNumber(num) + '';
  };

  const handleMemberClick = (memberId: string | number) => {
    navigate(`/team/${memberId.toString()}`);
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <Helmet>
        <title> - </title>
      </Helmet>

      <h1 className="text-3xl font-bold text-center mb-8"></h1>

      <div className="bg-blue-50 rounded-lg p-6 mb-12">
        <div className="grid grid-cols-2 gap-4 text-center">
          <div className="p-4">
            <p className="text-gray-600 mb-2"></p>
            <p className="text-3xl font-bold text-blue-600">{teamMembers.length} </p>
          </div>
          <div className="p-4">
            <p className="text-gray-600 mb-2"></p>
            <p className="text-3xl font-bold text-blue-600">
              {numberToChineseUpperCase(teamMembers.reduce((sum, member) => 
                sum + (typeof member.totalLoanAmount === 'number' ? member.totalLoanAmount : 0), 0
              ))}
            </p>
          </div>
        </div>
      </div>

      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
        {teamMembers.map(member => (
          <div
            key={member.id}
            className="bg-white rounded-lg shadow-lg p-6 cursor-pointer transition-transform hover:scale-105"
            onClick={() => handleMemberClick(member.id)}
          >
            <h2 className="text-xl font-bold mb-2">{member.name}</h2>
            <p className="text-gray-600 mb-2">{member.title}</p>
            <p className="text-gray-700 mb-4">{member.description}</p>
            
            <div className="mb-4">
              <p className="text-gray-600">
                <span className="font-semibold"></span>{member.companyName}
              </p>
              <p className="text-gray-600">
                <span className="font-semibold"></span>{member.location}
              </p>
            </div>

            <div className="mb-4">
              <p className="text-green-600">
                <span className="font-semibold"></span>{member.casesCompleted} 
              </p>
              <p className="text-green-600">
                <span className="font-semibold"></span>
                {(() => {
                  console.log(':', member.totalLoanAmount);
                  return typeof member.totalLoanAmount === 'number' 
                    ? numberToChineseUpperCase(member.totalLoanAmount)
                    : '';
                })()}
              </p>
            </div>

            <div className="flex flex-wrap gap-2">
              {member.specialties.map(specialty => (
                <span key={specialty} className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm">
                  {specialty}
                </span>
              ))}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Team; 