import React from 'react';
import { Helmet } from 'react-helmet-async';
import { loanData } from '../data/loanData';
import { convertToChineseNumber } from '../utils/numberFormat';

const LoanInProgress: React.FC = () => {
  // 
  const totalLoanAmount = loanData.reduce((sum, loan) => sum + (loan.investmentAmount || 0), 0);

  return (
    <div className="container mx-auto px-4 py-8">
      <Helmet>
        <title> - </title>
        <meta name="description" content="" />
      </Helmet>

      <div className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold"></h1>
        <div className="text-sm text-gray-500">
           {loanData.length}  {convertToChineseNumber(totalLoanAmount)}
          <span className="block text-right">({totalLoanAmount.toLocaleString()} )</span>
        </div>
      </div>

      <div className="overflow-x-auto">
        <table className="min-w-full bg-white rounded-lg overflow-hidden shadow-lg">
          <thead className="bg-gray-100">
            <tr>
              <th className="px-6 py-3 text-left text-gray-600"></th>
              <th className="px-6 py-3 text-left text-gray-600"></th>
              <th className="px-6 py-3 text-left text-gray-600"></th>
              <th className="px-6 py-3 text-left text-gray-600"></th>
              <th className="px-6 py-3 text-left text-gray-600"></th>
              <th className="px-6 py-3 text-left text-gray-600"></th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-200">
            {loanData.map(loan => {
              const mortgageStatus = [
                loan.firstMortgageAmount ? `${convertToChineseNumber(loan.firstMortgageAmount)}` : null,
                loan.secondMortgageAmount ? `${convertToChineseNumber(loan.secondMortgageAmount)}` : null,
                loan.thirdMortgageAmount ? `${convertToChineseNumber(loan.thirdMortgageAmount)}` : null,
              ].filter(Boolean).join('') || '';

              return (
                <tr key={loan.id} className="hover:bg-gray-50">
                  <td className="px-6 py-4">{loan.area}</td>
                  <td className="px-6 py-4">{loan.propertyType}</td>
                  <td className="px-6 py-4">
                    {loan.buildingSize && <div>: {loan.buildingSize}</div>}
                    {loan.buildingAge && <div>: {loan.buildingAge}</div>}
                  </td>
                  <td className="px-6 py-4">
                    {loan.appraisalValue ? (
                      <span className="text-green-600">
                        {convertToChineseNumber(loan.appraisalValue)}
                        <span className="text-gray-500 text-sm block">
                          ({loan.appraisalValue.toLocaleString()})
                        </span>
                      </span>
                    ) : (
                      <span></span>
                    )}
                  </td>
                  <td className="px-6 py-4">
                    {loan.investmentAmount ? (
                      <span className="text-blue-600">
                        {convertToChineseNumber(loan.investmentAmount)}
                        <span className="text-gray-500 text-sm block">
                          ({loan.investmentAmount.toLocaleString()})
                        </span>
                      </span>
                    ) : (
                      <span></span>
                    )}
                  </td>
                  <td className="px-6 py-4">
                    <span className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm">
                      {mortgageStatus}
                    </span>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default LoanInProgress; 